<script>
    import {mapGetters} from 'vuex';
    import { get } from '../../js/helpers/api'

    export default {
        name: "AppCabinet",
        data () {
            return {
                user_id: null,
                message: ''
            }
        },

        computed: {
           ...mapGetters('cabinet',['orders','intervals'])

       },

        created() {

            this.user_id= parseInt( localStorage.getItem('user_id'));

            let data={ 'user':this.user_id};

           get(`/api/cabinet/${this.user_id}`).then((res) =>
            {
                let mix=[];

                if(res.data.success)
                {

                    this.message='Ваш заказ принят !';
                     mix=[res.data.records, res.data.data];
                    this.$store.dispatch('cabinet/loadItems',  mix );
                }
            });
        },

    }
</script>

